import Dexie from 'dexie';
import dbloader from 'dbloader';

self.__infLoop = function () {};

var db = new Dexie('moodo2');
db.version(1).stores({
    documents: 'id',
    items: 'id',
    settings: 'id',
    caches: 'cid',
    user: 'id',
    boards: 'id',
    localStorage: 'id',
    contacts: 'id'
});

dbloader.load(db, (data) => {
    postMessage(data);
});
